//
// Buttons Theme
//

// Theme colors
@each $name, $value in $theme-colors {
  // Base
  .btn.btn-#{$name} {
    $color: var(--kt-#{$name}-inverse);
    $icon-color: var(--kt-#{$name}-inverse);
    $border-color: var(--kt-#{$name});
    $bg-color: var(--kt-#{$name});

    $color-active: var(--kt-#{$name}-inverse);
    $icon-color-active: var(--kt-#{$name}-inverse);
    $border-color-active: var(--kt-#{$name}-active);
    $bg-color-active: var(--kt-#{$name}-active);

    @include button-custom-variant(
      $color,
      $icon-color,
      $border-color,
      $bg-color,
      $color-active,
      $icon-color-active,
      $border-color-active,
      $bg-color-active
    );
  }

  // Light
  @if (theme-light-color($name)) {
    .btn.btn-light-#{$name} {
      $color: var(--kt-#{$name});
      $icon-color: var(--kt-#{$name});
      $border-color: var(--kt-#{$name}-light);
      $bg-color: var(--kt-#{$name}-light);

      $color-active: var(--kt-#{$name}-inverse);
      $icon-color-active: var(--kt-#{$name}-inverse);
      $border-color-active: var(--kt-#{$name});
      $bg-color-active: var(--kt-#{$name});

      @include button-custom-variant(
        $color,
        $icon-color,
        $border-color,
        $bg-color,
        $color-active,
        $icon-color-active,
        $border-color-active,
        $bg-color-active
      );
    }
  }

  // Background
  .btn.btn-bg-#{$name} {
    $color: null;
    $icon-color: null;
    $border-color: var(--kt-#{$name});
    $bg-color: var(--kt-#{$name});

    $color-active: null;
    $icon-color-active: null;
    $border-color-active: null;
    $bg-color-active: null;

    @include button-custom-variant(
      $color,
      $icon-color,
      $border-color,
      $bg-color,
      $color-active,
      $icon-color-active,
      $border-color-active,
      $bg-color-active
    );
  }

  // Active
  .btn.btn-active-#{$name} {
    $color: null;
    $icon-color: null;
    $border-color: null;
    $bg-color: null;

    $color-active: var(--kt-#{$name}-inverse);
    $icon-color-active: var(--kt-#{$name}-inverse);
    $border-color-active: var(--kt-#{$name});
    $bg-color-active: var(--kt-#{$name});

    @include button-custom-variant(
      $color,
      $icon-color,
      $border-color,
      $bg-color,
      $color-active,
      $icon-color-active,
      $border-color-active,
      $bg-color-active
    );
  }

  // Light
  @if (theme-light-color($name)) {
    .btn.btn-active-light-#{$name} {
      $color: null;
      $icon-color: null;
      $border-color: null;
      $bg-color: null;

      $color-active: var(--kt-#{$name});
      $icon-color-active: var(--kt-#{$name});
      $border-color-active: var(--kt-#{$name}-light);
      $bg-color-active: var(--kt-#{$name}-light);

      @include button-custom-variant(
        $color,
        $icon-color,
        $border-color,
        $bg-color,
        $color-active,
        $icon-color-active,
        $border-color-active,
        $bg-color-active
      );
    }
  }

  // Outline
  .btn.btn-outline-#{$name} {
    $color: var(--kt-#{$name});
    $icon-color: var(--kt-#{$name});
    $border-color: var(--kt-#{$name});
    $bg-color: transparent;

    $color-active: var(--kt-#{$name}-active);
    $icon-color-active: var(--kt-#{$name}-active);
    $border-color-active: var(--kt-#{$name});
    $bg-color-active: var(--kt-#{$name}-light);

    @include button-custom-variant(
      $color,
      $icon-color,
      $border-color,
      $bg-color,
      $color-active,
      $icon-color-active,
      $border-color-active,
      $bg-color-active
    );
  }
}

