  // Keenthemes solid input style
  --kt-input-solid-bg: #{$input-solid-bg};
  --kt-input-solid-bg-focus: #{$input-solid-bg-focus};
  --kt-input-solid-placeholder-color: #{$input-solid-placeholder-color};
