  --kt-nav-tabs-link-hover-border-color: #{$nav-tabs-link-hover-border-color};
  --kt-nav-tabs-link-active-color: #{$nav-tabs-link-active-color};
  --kt-nav-tabs-link-active-bg: #{$nav-tabs-link-active-bg};
