  // Dropdowns
  --kt-dropdown-box-shadow: #{$dropdown-box-shadow};
  --kt-dropdown-bg: #{$dropdown-bg};

  // Toasts
  --kt-toast-color: #{$toast-color};
  --kt-toast-background-color: #{$toast-background-color};
